/* Small Screen */
.container {
    margin: 5rem auto;
}

.movies {
    text-align: center;
}

.movies__title {
    margin-bottom: 1rem;
    font-size: 2.44rem;
    color: #4361ee;
}

.movie__container {
    display: flex;
    flex-direction: column;
}

/* Medium Screen */
@media (min-width: 768px) {
    .movie__container {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
    }
}

/* Large Screen */
@media (min-width: 992px) {
    .container {
        max-width: 1200px;
    }
}